@keyframes buttonAnime {
    0% {
        transform: scale(1);
      }
      8% {
        transform: scale(1.05);
      }
     15% {
        transform: scale(1);
      }
      25% {
        transform: scale(1.05);
      }
      40% {
        transform: scale(1);
      }
      100% {
        transform: scale(1);
      }
  }

.animated-button{
    animation: buttonAnime 2s infinite;
    animation-delay: 2s;
}

